import React from 'react';
import { Link } from 'react-router-dom';
import { FaFacebook, FaInstagram, FaLinkedin, FaTwitter } from 'react-icons/fa';
import { ReactElement } from 'react';
import styles from '../css/footer.module.css';
import { IconType } from 'react-icons/lib';
import Rings from './Ring';
import { footer as links } from '../utils/text';

function getIcon(name: string) {
	switch (name) {
	case 'FaFacebook':
		return FaFacebook;
	case 'FaInstagram':
		return FaInstagram;
	}
	return null;
}

export default function Footer(): ReactElement {
	
	return (
		<div className={styles.footer}>
			<div className={styles.row1}>
				<div className={styles.icons}>
					{Object.entries(links).map(([k, meta], i) => {
						if (k.startsWith('_')) return null;
						if (meta.hide) return null;
						let v = {
							icon: getIcon(meta.icon)
						};
						return (
							<a data-aos='zoom-in' data-aos-delay={100 * i} data-aos-offset={-50}
								key={k} id={k} href={meta.href} target='_blank' rel='noopener noreferrer'>
								{v.icon === null ?
									<img src={process.env.PUBLIC_URL + meta.icon} alt={k} /> :
									<v.icon />
								}
							</a>
						);
					})}
				</div>
			</div>
			<div className={styles.author} data-aos='zoom-in' data-aos-offset={-50}>
				{links._copyright}
			</div>
			<Rings className='bgRings' style={{ left: 'unset', right: '38%' }} />
		</div>
	);
}