import React, { ReactElement } from 'react';
import styles from '../css/home.module.css';
import { hasLaunched } from '../App';
import Contact from './Contact';
import List from './List';
import TextCarousel from './TextCarousel';
import Rings from '../components/Ring';
import { home } from '../utils/text';
import { isMobile } from '../utils/auth';

export default function Home(): ReactElement {

	return <>
		<section id='home' className={styles.container}>
			<div className='wrapper'>
				<h1 className={styles.title} data-aos='fade'>{home.home.title}</h1>
				<p className={styles.subtitle} data-aos='fade'>{home.home.subtitle}</p>
			</div>
			<Rings className='bgRings' />
		</section>
		<section id='works' className={styles.container}>
			<div className='wrapper'>
				{!isMobile() ?
					<TextCarousel withNumbers slides={home.carousel as unknown as any[]} /> :
					(home.carousel as unknown as any)?.map((data: { title: string, description: string, img: string }, i: number) => {
						return <div key={'mobile.card.' + i} className={styles.card} data-aos='fade-up'>
							<h2>{data.title}</h2>
							{data.description}
							<img src={process.env.PUBLIC_URL + data.img} />
						</div>;
					})
				}
			</div>
		</section>
		<section id='works-text' style={{display: !hasLaunched ? 'none' : undefined}} className={styles.container}>
			<div className='wrapper'>
				<h2 data-aos='fade'>How it works</h2>
				<List>
					{(home.list as unknown as string[])?.map((s, i) => (
						<li data-aos='fade-left' key={'line.' + i}>
							{s}
						</li>
					))}
				</List>
			</div>
		</section>
		<section id='partners' style={{display: !hasLaunched ? 'none' : undefined}} className={styles.container}>
			<div className='wrapper'>
				<h2>Partners</h2>
				<p>Here are our partners - these are the real OGs of ethical businesses</p>
				<p>Like what you see? Download the app now</p>
			</div>
		</section>
		<section id='faqs' style={{display: !hasLaunched ? 'none' : undefined}} className={styles.container}>
			<div className='wrapper'>
				<h2>FAQs</h2>
			</div>
		</section>
		<section id='notice' style={{display: hasLaunched ? 'none' : undefined}} className={styles.container}>
			<div className='wrapper'>
				<h2 data-aos='fade'>{home.notice.h2}</h2>
				<p data-aos='fade'>Follow us on Instagram{' '}
					<a href='https://instagram.com/ethicard.app' target='blank' rel='noopener noreferrer'>@ethicard.app</a>{' '}
					or like us on{' '}
					<a href='https://facebook.com/ethicard.app' target='blank' rel='noopener noreferrer'>Facebook</a>.
				</p>
			</div>
		</section>
		<Contact />
	</>;
}
